<template>
  <div>
    <div class="question-text" v-html="text"></div>

    <div class="response-body">
      <template v-for="(item, id) in items" :key="id">

      <div :id="'answer-item-' + item.id"
           v-show="!hideObjectDict['item-' + item.id]"
           class="item-container response-row">

        <div class="item-label-container item-label-start" v-html="item.label"></div>

        <div class="scale-container">
          <template v-for="(point, id) in scale">
            <div v-if="!point.secondary_point"
                 :key="id"
                 class="scale-label-container main-scale">
              <input type="radio"
                     :id="this.qid + '-' + item.id + '-' + point.value"
                     :name="item.id"
                     :value="point.value"
                     @change="responseChanged($event)"
                     @click="scrollToNext($event)">
              <label :for="this.qid + '-' + item.id + '-' + point.value"
                     class="scale-label"
                     :class="{ 'main-label': !point.secondary_point }">
                <span class="scale-label-span" v-html="point.input_label"></span>
              </label>
            </div>
          </template>
        </div>

        <div class="item-label-container" v-html="item.label_alt"></div>

        <div class="scale-container scale-container-secondary">
          <template v-for="(point, id) in scale">
            <div v-if="point.secondary_point"
                 :key="id"
                 class="scale-label-container secondary-scale">
              <input type="radio"
                     :id="this.qid + '-' + item.id + '-' + point.value"
                     :name="item.id"
                     :value="point.value"
                     @change="responseChanged($event)">
              <label :for="this.qid + '-' + item.id + '-' + point.value"
                     class="scale-label"
                     :class="{ 'main-label': !point.secondary_point }">
                <span class="scale-label-span" v-html="point.input_label"></span>
              </label>
            </div>
          </template>
        </div>

      </div>

      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SemanticDifferential',
  props: ['qid', 'text', 'items', 'scale', 'hideObjectDict'],
  emits: ['responseChanged'],
  data: function() {
    return {
      response: {},
      shouldScroll: window.innerWidth <= 768,
    }
  },
  created() {
    this.items.forEach(i => {
      this.response[i.id] = -99;
    })
    this.$emit('responseChanged', {id: this.qid, response: this.response, question: this.text, items: this.items});
  },
  mounted() {
    window.addEventListener('resize', this.updateScrollSetting);
    this.addClasses();
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.updateScrollSetting);
  },
  methods: {
    updateScrollSetting() {
      this.shouldScroll = window.innerWidth <= 768;
    },
    responseChanged(event) {
      this.response[event.target.name] = event.target.value;
      this.$emit('responseChanged', {id: this.qid, response: this.response, question: this.text, items: this.items});
    },
    getHeightOfLastQuestionTextBefore(element) {
      let questionBody = element.closest('.question-body');

      if (!questionBody) {
          return null;
      }
      const questionText = questionBody.querySelector('.question-text');
      return questionText ? questionText.offsetHeight : 0;
    },
    scrollToNext(element) {
      // Scroll to next item if it exists.
      const currentRow = event.target.closest('div.response-row');
      if (currentRow) {
        const nextRow = currentRow.nextElementSibling;
        if (nextRow) {
          const stickyHeight = this.getHeightOfLastQuestionTextBefore(currentRow);
          const nextRowTop = nextRow.getBoundingClientRect().top + window.scrollY;
          const adjustedPosition = nextRowTop - stickyHeight;
          window.scrollTo({ top: adjustedPosition, behavior: 'smooth' });

        } else if (nextRow === null) {
          // Scroll to next question if there is no next row.
          let questionBody = currentRow.closest('.question-app-container');
          let nextQuestionBody = questionBody.nextElementSibling;

          if (nextQuestionBody) {
            const nextQuestionTop = nextQuestionBody.getBoundingClientRect().top + window.scrollY;
            window.scrollTo({ top: nextQuestionTop, behavior: 'smooth' });
          }
        }
      }
    },
    addClasses() {
      const containers = document.querySelectorAll('.item-container');

      containers.forEach(container => {
        const mainScales = container.querySelectorAll('.main-scale');

        if (mainScales.length > 0) {
          // Remove old classes in case of re-renders
          mainScales.forEach(el => {
            el.classList.remove("main-scale-first", "main-scale-last");
          });

          // Add class to first .main-scale
          mainScales[0].classList.add("main-scale-first");

          // Add class to last .main-scale (if different from first)
          if (mainScales.length > 1) {
            mainScales[mainScales.length - 1].classList.add("main-scale-last");
          }
        }
      });
    }
  },
}
</script>

<style scoped>
.item-container {
  display: flex;
  flex-direction: column;
  padding-bottom: 20px;
  padding-top: 70px;
  border-bottom: 1px solid #ededed;
  width: 70%;
  text-align: center;
  margin: auto;
}

.item-label-container {
  padding-bottom: 15px;
}

.item-label-start {
  padding-bottom: 15px;
}

.scale-label-container {
  flex: 1;
  width: 100%;
  padding-top: 2px;
  padding-bottom: 2px;
}

.scale-label {
  cursor: pointer;
  width: 100%;
  height: 100%;
  padding: 5px;
  text-align: center;
  background: #eaeaea;
  text-wrap: auto;
  word-wrap: break-word;
  overflow-wrap: break-word;
  white-space: normal;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
  font-size: 0.9rem;
}

.scale-label:hover {
  background: #cfcfcf;
}

.main-scale {}

.main-scale-first label {
  border-radius: 5px 5px 0 0;
}

.main-scale-last label {
  border-radius: 0 0 5px 5px;
}
.main-scale-last {
  margin-bottom: 15px;
}

.scale-container-secondary {
  padding-top: 30px;
}

.secondary-scale > label {
  background: #f4f4f4;
  border-radius: 5px;
}

input[type="radio"] {
  display: none;
}

input[type="radio"]:checked + label {
  background-color: #45819e;
  color: white;
}

@media (min-width: 769px) {
  .item-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-bottom: 20px;
    padding-top: 20px;
    border-bottom: 1px solid #ededed;
    width: 100%;
  }

  .item-label-start {
    text-align: right !important;
    padding-right: 8px;
    justify-content: end;
  }

  .scale-container {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    min-height: 40px;
    width: 100%;
    height: 100%;
  }

  .scale-container-secondary {
    max-width: 10%;
    padding-top: 0;
  }

  .scale-label-container {
    flex: 1;
    width: 100%;
    padding-left: 2px;
    padding-right: 2px;
    text-wrap: auto;
    word-wrap: break-word;
    overflow-wrap: break-word;
    white-space: normal;
  }

  .scale-label {}

  .item-label-container {
    width: 20%;
    max-width: 20%;
    text-align: left;
    padding-bottom: 0;
    display: flex;
    align-items: center;
    overflow-wrap: anywhere;
    hyphens: auto;
  }

  .main-scale-first label {
    border-radius: 5px 0 0 5px;
  }

  .main-scale-last label {
    border-radius: 0 5px 5px 0;
  }

  .main-scale-last {
    margin-right: 8px;
    margin-bottom: 0;
  }
}
</style>
